import { Link } from 'gatsby';
import styled from 'styled-components';
import { withGlobalStyle } from '../hoc';
import routes from '../routes';

const FullPageDiv = styled.div`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 10%;
  box-sizing: border-box;
  background-color: ${(props) => props.theme.lightBackgroundColor};
`;

const NotFoundCode = styled.h1`
  font-size: 3.75rem;
  color: ${(props) => props.theme.remarksTextColor};
  line-height: 1;
  margin: 0 0 1.875rem 0;
  font-weight: normal;
`;

const NotFoundTitle = styled.h2`
  font-size: 1.6875rem;
  color: ${(props) => props.theme.remarksTextColor}
  margin: 0 0 1.6875rem 0;
  font-weight: normal;
`;

const NotFoundDescriptions = styled.p`
  font-size: 1.125rem;
  margin: 0;
`;

const NotFoundPage = () => (
  <FullPageDiv>
    <NotFoundCode>404</NotFoundCode>
    <NotFoundTitle>Page Not Found</NotFoundTitle>
    <NotFoundDescriptions>
      Sorry, the page you are looking for could not be found. You can
      {' '}
      <Link to={routes.home}>click here</Link>
      {' '}
      to head to homepage.
    </NotFoundDescriptions>
  </FullPageDiv>
);

export default withGlobalStyle(NotFoundPage);
